body{
  margin: 0;
}
html{
  font-size: 17px;
}
.container{
  width: 95%;
  margin: auto;

}
a{
  text-decoration: none;
  color: #fff;
}


/* !hero Section */
.hero{
  width: 100%;
  height: 830px ;
  margin: auto;
  margin-top: 10px;
  padding: .4%;
  border-radius: 5px;
  background-color: rgba(240, 248, 255, 0.532);
  /* background-color: rgb(158, 158, 158); */
}
/* *NavBarrrr !!!!!!!!!!!!!!!!!!!!!!! */
.Navbar{
  width: 100%;
  height: 90px;
  position: absolute;
  top: 0;
  color: #fff;
  border-bottom: 1.6px solid #ffffff36;
  display: flex;
  align-items: center;
}
.navImp{
  position: absolute;
  left: 4%;
  display: flex;
  align-items: center;
  gap: 10%;
}
.menuIcon{
  display: none;
}
.navbarBrand{
  color: rgb(0, 242, 255);
  /* color: rgb(0, 242, 255); */
  font-weight: 700;
  font-size: 3.5rem;
  text-decoration: none;
}
.empty-logo{
  width: 95px;
  height: 11px;
  background-color: rgba(240, 253, 255, 0.532);
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  position: absolute;
  top: 0px;
  left: 0;
  right: 0;
  margin: auto;
  display: flex;
  justify-content: center;
  gap: 10px;
}
.empty-logo-item1{
  height: 5px;
  width: 25px;
  border-radius: 5px;
  background-color: rgba(240, 248, 255, 0.532);
/* position: absolute; */
}
.empty-logo-item2{
  height: 5px;
  width: 5px;
  border-radius: 5px;
  background-color: rgba(240, 248, 255, 0.532);
}

.navbarItems{
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  /* background-color: aqua; */
  position: relative;
}
.menuItems{
  height: 100%;
  margin: 0;
  padding: 0;
  gap: 20%;
  display: flex;
  align-items: center;
  list-style: none;
  /* background-color: aquamarine; */
  color: #fff;
}
.menuItem{
   color: #fff;
}
.search-container{
  position: absolute;
  right: 4%;
}
.searchBtn{
  border: none;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #ffffff29;
  color: #fff;
}



.searchBarContainer{
  position: relative;
  display: none;
}




/* TODO Sidenav */
#sideNav{
  display:none;
  /* position: -webkit-sticky; */
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  backdrop-filter: blur(10px);
  background-color:#000d155b;
  z-index: 1;
  font-size: 2rem;
  color: #fff;
}
.sideNavContainer{
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  width: 400px;
  height: 100vh;
  box-shadow: 0 0 2px #fff;
  background-color:#000d15af;
  /* backdrop-filter: blur(20px); */
  overflow-y: scroll;
}
.sidnavBackBtn{
  box-shadow: 0 0 2px #fff;
  padding: 4% 10%;
  font-weight: 700;
}
.sideNavLinks{
  display: flex;
  flex-direction: column;
  list-style: none;
  padding: 0%;
  margin: 0;
}
.sideNavLinks li{
  padding: 5% 10%;
}


