.inner-hero{
  width: 100%;
  height: 100%;
  margin: auto;
  border-radius: 3.5px;
  /* box-shadow: 0 0 20px; */
  /* transform: skew(-10deg); */
  background-color:#000d15 ;
  background: linear-gradient(90deg, #00060a  1%#001725 70%);
  position: relative;
}
.inner-hero-img{
  position: absolute;
  width: 75%;
  height: 75%;
  right: 0;
  top: 10px;
  /* z-index: 0; */
}
.backImg{
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.inner-hero-img::before{
    content: "";
    position: absolute;
    height: 100%;
    width: 100%;
    background: rgb(43, 42, 60);
    /* background: linear-gradient(90deg, rgb(134, 134, 134) 0%, rgba(43, 42, 60, 0) 100%); */
    background: linear-gradient(0deg, #000d15f5  2%, rgba(43, 42, 60, 0) 20%),
                linear-gradient(25deg, #000d15f5  10%, rgba(43, 42, 60, 0) 40%),
                linear-gradient(90deg, #000d15f9  4%, rgba(43, 42, 60, 0) 40%),
                linear-gradient(155deg, #000d15f5  5%, rgba(43, 42, 60, 0) 40%),
                linear-gradient(180deg, #000d15f5  3%, rgba(43, 42, 60, 0) 15%),
                /* linear-gradient(205deg, #000d15f5  3%, rgba(43, 42, 60, 0) 15%), */
                linear-gradient(270deg, #000d15f5  1%, rgba(43, 42, 60, 0) 8%);
    /* box-shadow: 0 0 100px inset; */
}

.Movies-container{
  width:100%;
  height: 90%;
  position:absolute;
  bottom: 0 ;
  display: flex;
  /* background-color:#f3f3f3c3; */
  align-items: end;
}

.movieinfo-container{
  position: absolute;
  /* background-color: rgba(127, 255, 212, 0.321); */
  left:10%;
  top: 10%;
  width: 40%;
  height: 70%;
  color: #fff;  
}
.movieTitle{
  font-size: 3.5rem;
  font-weight: 700;
  line-height: 90%;
  margin: 0;
}
.rating-star{
  margin-top: 5%;
}
.movie-genre{
  display:flex;
  gap:5%;
  margin-top:2%;
  line-height: 0;

}
.movieInfoBtn{
  width: 75%;
  height: 10%;
  margin-top: 5%;
  padding: 0;
  display: flex;
  text-decoration: none;
}
.btn{
  width: 65px;
  height: 100%;
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
.btn1{
  background-color:rgb(0, 242, 255);
  border-radius: 0;
}
.btn1 img{
  width: 50%;
  height: 100%;
}
.btn2{
  background-color:rgb(0, 95, 100) ;
  border-radius: 0;
  color: #fff;
  padding: 10px;
  font-size: 1.6rem;
  position: relative;
  /* margin-left: 2px; */
}
.movieDisc{ 
  width: 100%;
  height: 100%;
  /* background-color: #fff; */
  color: #ffffff94;
  overflow: hidden;

}
.movieDisc::before{
  content: "";
    position: absolute;
    height: 100%;
    width: 100%;
    /* background: rgb(43, 42, 60); */
    background: linear-gradient(25deg, #000d15 20%, #03001600 90%);
}
.Movies-inner-container{
  /* position: relative; */
  width: 100%;
  height: 35%;
  display: flex;
  padding-left: 0 ;
  margin: 0;
  margin-bottom: 2%;
  /* background-color:#ef2c2ccb; */
  /* margin-left: 15%; */
  /* justify-content: space-evenly; */
  overflow-x:auto;
  align-items: end;
  gap: 2.8%;
}
.Movies-inner-container::-webkit-scrollbar{
  display: none;
}


.Movie-container{
  /* margin: 0 10px; */
  cursor: pointer;
  /* background-color: aqua; */
  position: relative;
  display: flex;
  flex-direction: column  ;
  justify-content: end;
}
.movie-poster-container{
  padding: 0.05rem;
  background-color: #ffffff56;
}
.hero-movie-poster{
  /* position: absolute; */
  box-shadow: 0 0 50px inset #fff;
  width: 100%;
  height:80%;

}
.Movie-container .Movie-txt-container{
  color:#fff;
  width: 5.8rem;
  height: 1.5rem;
  /* background-color: lime; */
  position: relative;
  overflow: hidden;
}


/* ! Hero Ends  */


/* !Trending Movies  */

.trendingMovieContainer{
  width: 100%;
  height: 400px;
  margin: auto;
  margin-top: 4%;
  position: relative;
  /* margin-bottom: 100px; */
  /* background-color: aliceblue; */
}
.trending-txt{
  color: rgb(0, 242, 255);
  font-size: 2rem;
}
.trendingMovieList{
  display: flex;
  height: 100%;
  /* background-color: blue; */
  background-color: #000d15;

  overflow-x: scroll;
  padding: 0px;
  gap: 1%;
}
.trendingMovieList::-webkit-scrollbar{
  display: none;
}
.trendingMovie{
  /* background-color: aqua; */
  height: 100%; 
}
.trending-movie-poster{
  height: 18rem;
   width: 12rem;
}

/* //!Latest movie container */
.latestMoviezcontainer{
width: 100%;
margin: auto;
margin-top: 5%;
}
.movie-page{
  margin-top: 5%;
}
.grid{
  width: 100%;
  position: relative;
  /* padding: 2.5% ; */
  /* background-color: #a4a480; */
  display:flex;
  flex-wrap: wrap;
  justify-content: center;
}
.card{
  /* background-color: #a4a480; */
  color:#fff;
  margin: 5px;

}
.card-info{
  position: relative;
  /* background-color: rgb(212, 212, 212); */
  box-shadow: 0 0 2px #fff;
  /* width: 12rem;
  height: 20rem; */
  /* margin: 3.5px; */
  padding: 10px;
  display: flex;
  border-radius: 4px;
  flex-direction: column;
  align-items: center;
  /* align-items:; */
}

.card-info .poster{
  position: relative;
  background-color: transparent;
  display: flex;
  justify-content: center;
}
.card-info .disc{
  /* background-color: aqua; */
  color: #fff;
  overflow: hidden;
  height: 2rem;
  width: 12rem;
  font-size: 1.5rem;
  text-decoration: none;
}
.card-info .poster img{
  width: 12rem;
  height: 17rem;
  border-radius: 3px;
}

/* App Promotion csss */
.appPromoContainer{
  display: none;
  position: relative;
  width: 100%;
  height: 200px;
  margin-top: 5%;
  background-color: rgb(203, 2, 2);
}
.appPromoContainer::before{
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(155deg, #000d15e8 0%,#000d1500 100% );
  /* box-sizing: border-box; */

}
.inner-appPromoContainer{
  width: 96%;
  height: 90%;
  border: 1px solid #ffffff93;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  color: #fff;
  font-size: 1.7rem;
  display: flex;
}
.promo-txt{
  /* background-color: aqua; */
  padding: 4%;
  /* background-color: aquamarine; */
  width: 50%;
}
.promo-txt p{
  color: #ffffffb0;
  font-size: 2rem;
}
.promo-txt button{
  border: none;
  background-color: transparent;
  font-size: 3rem;
  color: #fff;
  cursor: pointer;
}
.greaterThan{
  font-size: 2rem;
}
.inner-appPromoContainer h1{
  margin: 0;
}
.appPromo-img{
  /* width: 200px;
  height: 100%; */
  /* background-color: aquamarine; */
  display: flex;
  position: absolute;
  right: 5%;
  bottom: 0;
  rotate: -10deg;
}
.appPromo-img{
  width:150px;
  height: 140px;


}


/* WebPromotion cssss */
.webPromoContainer{
  position: relative;
  width: 100%;
  height: 120px;
  margin-top: 5%;
  background-color: rgb(0, 29, 175);
  display: flex;
  justify-content: center;
  align-items: center;
}
.inner-webPromoContainer{
  margin: 0;
  padding: 0;
  height: 75%;
  width: 93%;
  background-color:rgb(2, 35, 200) ;
  display: flex;
  color: #fff;
}
.webPromo-txt{
  margin: 0;
  padding: 0;
  line-height: 0;
  /* background-color: aqua; */
  display: flex;
  flex-direction: column;
}
.webPromo-btn{
  display: flex;
}
.webPromo-btn button{
  border: none;
  font-size: 1.4rem;
  border-radius: 50px;
  font-weight: 900;
  background-color: rgb(255, 221, 0);
  margin-left: 8px;
  margin-bottom: 4px;
}



@media  (max-width:580px) {
  html{
    font-size: 7px;
  }
  .container{
    width: 100%;
  }
  .hero{
  width: 100%;
  height: 330px ;
  margin: 0;
  padding: 0;
  }
  .Navbar{
    font-size: 15px;
    height:16% ;
  }
  .navImp{
    left: 10px;
  }
  .menuIcon{
  display: block;
  color: #fff;
  font-size: 5rem;
  margin-right: 10px;

  }
  .empty-logo{
    display: none;
  }
  .menuItems{
    display: none;
  }
  .movieinfo-container{
    font-size: 15px;
    top: 18%;
    left: 8%;
  }
  /* .movieTitle{
    font-size: 2rem;
  }*/
  .Navbar{
    height: 50px;
  }

  .NavbarContainer{
    position:relative;
    /* background-color: #823939; */
    width: 100%;
    height:50px;
  }
  .searchBarContainer{
    display: none;
    padding: 2%;
    z-index: 1;
  }
  .inner-searchBarContainer{
    display: flex;
  }
  .searchBar{
    width: 100%;
    height: 40px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    padding-left: 10px;
    font-size: 3rem;
    outline: none;
  }
  .cancel-search{
    /* width: 40px; */
    background-color: rgb(13, 246, 250);
    border: none;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    font-size: 3rem;
  }

  /* todo SideNav */
  #sideNav{
    font-size: 4rem;
    z-index: 2;
  }
  .sideNavContainer{
    width: 250px;
  }
  /* .Movies-container{
    height: 79%;
  } */
  .ratings-container{
    display: flex;
    align-items:center;
    gap: 15%;
  }
  .rating-star{
    margin-top: 0;
    font-size: 01rem;
  }
  
  .movie-genre{
    margin-top:0;
  }
  .movieInfoBtn{
    height:18% ;
  }
  .btn{
    width: 45px;
  }
  .btn2{
    font-size: 2rem;
    padding: 7px;
  }
  .Movie-container .Movie-txt-container{
    /* font-size: 1.5rem; */
    display: none;
  }
 
  /* Trending movies container */
  .trendingMovieContainer{
    height: 250px;
  }
  .trending-movie-poster{
    height: 30rem;
     width: 20.5rem;
  }
  .trending-txt{
    font-size: 4rem;
  }


  /* Latest Update Movie */
  /* .card-info{
    width: 25rem;
    height: 40rem;
  } */
  .card-info .poster img{
    width: 25rem;
    height: 36rem;
  }
  .card-info .disc{
    font-size: 3.5rem;
    width: 25rem;
    height: 4rem;
  }


  /* App Promo Query */
  .appPromoContainer{
    display: block;
    height: 150px;
  }


  /* Web Promotion Queryy */
  .webPromoContainer{
    height: 60px;
  }
  .webPromo-txt{
    font-size: 1.3rem;
  }
}



